import React, { useEffect, useState } from "react";
import axios from "axios";
import styles from "./AllUsers.module.css";
import { useApiUrl } from "../../hooks/ApiUrl/ApiProvider";
import LodingCamera from "../../Library/LoadingCamera"; //      

const AllUsers = () => {
  const [users, setUsers] = useState([]);
  const [albums, setAlbums] = useState([]); //     
  const [selectedUserForEdit, setSelectedUserForEdit] = useState(null); // 
  const [selectedUserForAlbums, setSelectedUserForAlbums] = useState(null); // 

  const [editFormData, setEditFormData] = useState({
    name: "",
    email: "",
    phoneNumber: "",
    role: "",
  }); //  

  const apiUrl = useApiUrl(); //    -API 
  const [loading, setLoading] = useState(false); //   

  useEffect(() => {
    const fetchUsers = async () => {
      setLoading(true); //  

      try {
        const token = localStorage.getItem("token");
        const response = await axios.get(`${apiUrl}/api/admin/users`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setUsers(response.data);
      } catch (error) {
        console.error(
          "Error fetching users:",
          error.response?.data || error.message
        );
      } finally {
        setLoading(false); //  
      }
    };

    fetchUsers();
  }, [apiUrl]);

  const fetchAlbums = async (userId) => {
    if (selectedUserForAlbums === userId) {
      //    ,    
      setSelectedUserForAlbums(null);
      setAlbums([]);
    } else {
      setLoading(true); //  

      try {
        const token = localStorage.getItem("token");
        const response = await axios.get(
          `${apiUrl}/api/admin/users/${userId}/albums`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        setAlbums(response.data);
        setSelectedUserForAlbums(userId); //    
      } catch (error) {
        console.error(
          "Error fetching albums:",
          error.response?.data || error.message
        );
      } finally {
        setLoading(false); //  
      }
    }
  };

  const handleEditClick = (user) => {
    if (selectedUserForEdit === user._id) {
      //        
      setSelectedUserForEdit(null);
      setEditFormData({
        name: "",
        email: "",
        phoneNumber: "",
        role: "",
      });
    } else {
      //      
      setSelectedUserForEdit(user._id);
      setEditFormData({
        name: user.name,
        email: user.email,
        phoneNumber: user.phoneNumber || "",
        role: user.role,
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEditFormData({ ...editFormData, [name]: value });
  };

  const handleSave = async () => {
    setLoading(true); //  

    try {
      const token = localStorage.getItem("token");
      await axios.put(
        `${apiUrl}/api/admin/users/${selectedUserForEdit}`,
        editFormData,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setUsers((prevUsers) =>
        prevUsers.map((user) =>
          user._id === selectedUserForEdit ? { ...user, ...editFormData } : user
        )
      );
      setSelectedUserForEdit(null); //   
    } catch (error) {
      console.error(
        "Error updating user:",
        error.response?.data || error.message
      );
    } finally {
      setLoading(false); //  
    }
  };
  if (loading) return <LodingCamera />;
  return (
    <div className={styles.container}>
      <h1 className={styles.title}> </h1>
      <div className={styles.users}>
        <ul className={styles.userList}>
          {users.map((user) => (
            <li key={user._id} className={styles.userItem}>
              <span>: {user.name}</span>
              <span>: {user.email}</span>
              <span>: {user.phoneNumber || " "}</span>
              <span>: {user.role}</span>
              <button onClick={() => fetchAlbums(user._id)}>
                {selectedUserForAlbums === user._id
                  ? " "
                  : " "}
              </button>
              <button onClick={() => handleEditClick(user)}>
                {selectedUserForEdit === user._id ? " " : " "}
              </button>

              {/*       */}
              {selectedUserForEdit === user._id && (
                <div className={styles.editForm}>
                  <h2> </h2>
                  <label>
                    :
                    <input
                      type="text"
                      name="name"
                      value={editFormData.name}
                      onChange={handleInputChange}
                    />
                  </label>
                  <label>
                    :
                    <input
                      type="email"
                      name="email"
                      value={editFormData.email}
                      onChange={handleInputChange}
                    />
                  </label>
                  <label>
                    :
                    <input
                      type="text"
                      name="phoneNumber"
                      value={editFormData.phoneNumber}
                      onChange={handleInputChange}
                    />
                  </label>
                  <label>
                    :
                    <select
                      name="role"
                      value={editFormData.role}
                      onChange={handleInputChange}
                    >
                      <option value="user"></option>
                      <option value="business"></option>
                      <option value="admin"></option>
                    </select>
                  </label>
                  <button onClick={handleSave}></button>
                </div>
              )}

              {/*     */}
              {selectedUserForAlbums === user._id && (
                <div className={styles.albums}>
                  <h2>  </h2>
                  {albums.length > 0 ? (
                    <ul className={styles.albumList}>
                      {albums.map((album) => (
                        <li key={album._id} className={styles.albumItem}>
                          <h3>  : {album.eventName}</h3>
                          <p>
                            : {new Date(album.date).toLocaleDateString()}
                          </p>
                          <p>: {album.location}</p>
                          <p> : {album.eventType}</p>
                        </li>
                      ))}
                    </ul>
                  ) : (
                    <p>   .</p>
                  )}
                </div>
              )}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default AllUsers;
