import React, { useState } from "react";
import styleStepOne from "./Step1.module.css"; //   CSS Module

const Step1 = ({
  formData = {},
  handleChange = () => {},
  handleCoverImageChange = () => {},
}) => {
  const [successMessage, setSuccessMessage] = useState(false); //   

  const saveStep1Data = () => {
    //      
    const requiredFields = ["eventName", "location", "date", "eventType"];
    const missingFields = requiredFields.filter(
      (field) => !formData[field] || formData[field].trim() === ""
    );

    if (missingFields.length > 0) {
      console.log(" :", missingFields); //     
      return;
    }

    //    ,   
    setSuccessMessage(true);
    setTimeout(() => {
      setSuccessMessage(false); //    1.5 
    }, 1500);

    console.log("   .  ...");
  };

  return (
    <div className={styleStepOne.stepOneContainer}>
      {/*   */}
      {successMessage && (
        <div className={styleStepOne.successMessage}>  !</div>
      )}

      <h2 className={styleStepOne.stepHeader}> 1:  </h2>
      <form className={styleStepOne.stepForm}>
        {/* :   */}
        <div className={styleStepOne.formGroup}>
          <label htmlFor="eventName" className={styleStepOne.formLabel}>
             :
          </label>
          <input
            type="text"
            id="eventName"
            name="eventName"
            value={formData.eventName}
            onChange={handleChange}
            className={styleStepOne.formInput}
            placeholder=" "
            required
          />
          {!formData.eventName && (
            <p className={styleStepOne.errorMessage}>   </p>
          )}
        </div>

        {/* :  */}
        <div className={styleStepOne.formGroup}>
          <label htmlFor="location" className={styleStepOne.formLabel}>
             :
          </label>
          <input
            type="text"
            id="location"
            name="location"
            value={formData.location}
            onChange={handleChange}
            className={styleStepOne.formInput}
            placeholder=" "
            required
          />
          {!formData.location && (
            <p className={styleStepOne.errorMessage}>  </p>
          )}
        </div>

        {/* :  */}
        <div className={styleStepOne.formGroup}>
          <label htmlFor="date" className={styleStepOne.formLabel}>
            :
          </label>
          <input
            type="date"
            id="date"
            name="date"
            value={formData.date}
            onChange={handleChange}
            className={styleStepOne.formInput}
            required
          />
          {!formData.date && (
            <p className={styleStepOne.errorMessage}>  </p>
          )}
        </div>

        {/* :   */}
        <div className={styleStepOne.formGroup}>
          <label htmlFor="eventType" className={styleStepOne.formLabel}>
             :
          </label>
          <select
            id="eventType"
            name="eventType"
            value={formData.eventType}
            onChange={handleChange}
            className={styleStepOne.formInput}
            required
          >
            <option value="">  </option>
            <option value="wedding"></option>
            <option value="party"></option>
            <option value="barMitzvah"> </option>
          </select>
          {!formData.eventType && (
            <p className={styleStepOne.errorMessage}>   </p>
          )}
        </div>

        {/*   */}
        <div className={styleStepOne.formGroup}>
          <label htmlFor="coverImage" className={styleStepOne.formLabel}>
               !:
          </label>
          <input
            type="file"
            id="coverImage"
            name="coverImage"
            onChange={handleCoverImageChange}
            className={styleStepOne.formFileInput}
          />
        </div>
      </form>

      {/*    */}
      <button onClick={saveStep1Data} className={styleStepOne.saveButton}>
         
      </button>
    </div>
  );
};

export default Step1;
